body {
  background-color: rgb(239, 232, 210);
  color: rgb(114, 36, 36);

  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.form {
  margin: 0 auto;
  width: 90%;

  border: 3px solid rgb(114, 36, 36);

  padding: 2em;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.section {
  border: 2px solid rgb(235, 220, 191);
  padding: 2em;
}

.question--container {
  width: 500px;

  display: flex;
  justify-content: space-between;
}

/*=====  Input  ======*/

.question--container > input {
  width: 300px;
}

input[type='number'] {
  width: 15em;
  height: 3em;
}

.array--input > input {
  width: 300px;
  height: 3em;
}

.value_and_unit > input[type='number'] {
  width: 150px;
}

.value_and_unit > select {
  width: 150px;
}

/*=====  Radio  ======*/

.boolean--radio {
  width: 300px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.boolean--radio--content--container {
  margin-left: 10px;
}

.boolean--radio--content--container > input {
  width: 2em;
  height: 2em;
}

/*=====  Checkbox Header  ======*/
.checkbox--section {
  display: flex;
  flex-direction: column;

  width: 300px;
}

.checkbox--container {
  font-size: 1.25em;
  display: flex;
  justify-content: space-between;
}

.checkbox--container > input {
  width: 2em;
  height: 2em;
}

/*=====  Select  ======*/

select {
  width: 300px;
  height: 3em;
}
